@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
  color: #0D2936;
  position: relative;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
