.button {
  display: inline-block;
  width: 12.625rem;
  padding: 1rem 5.5rem 1rem 2.3125rem;
  text-align: center;
  background-color: #408F95;
  border: 1px solid #408F95;
  color: #fff;
  font-size: 0.875rem;
  line-height: 1.4;
  font-weight: 300;
  font-family: "Gotu", sans-serif;
  transition: color 0.3s, background-color 0.3s;
  position: relative;
  z-index: 1;
}

.button.button--green {
  background-color: #fff;
  border: 1px solid #fff;
  color: #408F95;
}

.button.button--green span {
  letter-spacing: 0.05em;
}

.button.button--green span.big {
  text-transform: uppercase;
}

.button::first-letter {
  text-transform: capitalize;
}

.button::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.3s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button.button--green::after {
  background: #408F95;
}

.button span::after {
  content: "";
  width: 2.5rem;
  height: 0.375rem;
  background: url(../images/common/button-arrow-white.png) no-repeat;
  background-size: contain;
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
  right: 2.25rem;
  transition: background-image 0.3s;
}

.button.button--green span::after {
  background: url(../images/common/button-arrow-green.png) no-repeat;
  background-size: contain;
  top: 48%;
}

.button::before {
  display: inline-block;
  content: "";
  width: 12.625rem;
  height: 3.375rem;
  background-color: transparent;
  border: 1px solid #408F95;
  position: absolute;
  top: 0.1875rem;
  left: 0.125rem;
  z-index: -2;
}

.button.button--green::before {
  background-color: transparent;
  border: 1px solid #fff;
}

.button:hover {
  background-color: #fff;
  color: #408F95;
}

.button.button--green:hover {
  background-color: #408F95;
  color: #fff;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button:hover span::after {
  background-image: url(../images/common/button-arrow-green.png);
}

.button.button--green:hover span::after {
  background-image: url(../images/common/button-arrow-white.png);
}

